class Program extends Component {
  render() {
    return (
      <div>
        <Card title="Chương trình đạo tạo">
          <Row>
            <Col style={{ float: 'right' }}>
              <Dropdown overlay={ActionOther}>
                <Button type="primary">
                  Thao tác khác
                  <Icon type="down" />
                </Button>
              </Dropdown>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <FormSearch />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={datachuyennganh} columns={columnsChuyen} />
            </Col>
          </Row>
        </Card>
        <Card title="">
          <Row>
            <Col span={24}>
              <FormSearchMon />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table scroll={{ x: true }} bordered dataSource={dataprogram} columns={columnsMon} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={this.cancelUpload} />
            </Col>
          </Row>
        </Card>
      </div>
    );
  }
}

export default props => (
  <Page {...props}>
    <Program />
  </Page>
);



