import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Khối kiến thức">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Kỳ thứ">{getFieldDecorator('kt', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Mã học phần">{getFieldDecorator('mh', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Tên học phần">{getFieldDecorator('thp', {})(<Input />)}</Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



